<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <div class="instructions">
    <h4>steps to get started</h4>
    <ol>
      <li>Buy equipment</li>
        <ul>
          <li>Raspberry Pi</li>
          <li>Interface board</li>
          <li>Create 2 Roomba Robot</li>
        </ul>
      <li><a href="link">Download</a> and install Roombaide</li>
      <li>Start coding!</li>
  </ol>
  </div>
    <a href="https://github.com/roombaide/roombaide">github </a>


  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Roombaide'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: disc;

}

li {
  padding:3%;
  text-align: left;
}
.instructions{
  width: 400px;
  height: 400px;
  margin: auto;

}

a {
  color: #027943;
}
</style>



